body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
        "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
        sans-serif;
    font-display: fallback;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Internet Explorer */
    -khtml-user-select: none; /* KHTML browsers (e.g. Konqueror) */
    -webkit-user-select: none; /* Chrome, Safari, and Opera */
    -webkit-touch-callout: none; /* Disable Android and iOS callouts*/
}
.dashboard-page{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 50px calc(100vw - 84px) 150px 120px;
    grid-row-gap: 15px;
    row-gap: 15px;
    grid-column-gap: 15px;
    -webkit-column-gap: 15px;
            column-gap: 15px;
    padding: 40px 40px 40px 40px;
    background: linear-gradient(180deg, #FC8A8A 0%, #FFD3B3 50%);
    background-size: 100vw 190px;
    background-repeat: no-repeat;
}
.chart-holder{
    z-index: 0;
    border: 2px solid #FFDCDC;
    grid-column: 1/4;
    grid-row: 2/3;
}
.dashboard-page--title{
    grid-column: 1/4;
    grid-row: 1/2;
}
.month-total-money{
    text-align: center;
    background-color: #FFAFA4 !important;
    grid-column: 1/3;
    grid-row: 3/4;
}
.month-total-money > p {
    color: white;
    font-size: 12px;
    padding: 15px 20px 10px 20px;
}
.month-drink-limit{
    background: linear-gradient(180deg, #FFDCDC 0%, #FFDCDC 50%) no-repeat;
    background-position-y: 300px;
    background-size: 100% 100%;
    text-align: center;
    padding: 80px 3px 0 3px;
    border: 2px solid #FFDCDC;
    grid-column: 3/4;
    grid-row: 3/5;
    transition: all 1s;
}
.month-drink-limit > p{
    color: #FC8A8A;
    font-size: 12px;
    mix-blend-mode: darken;
}
.month-total-drinks{
    padding: 20px;
    text-align: center;
    grid-column: 1/3;
    grid-row: 4/5;
}
.daily-chart{
    touch-action: none;
    grid-column: 1/4;
}
.month-total-drinks > p {
    color: #FC8A8A;
}
.month-total-drinks > h2 {
    color: #FC8A8A;
}
.chard-holder-description{
    font-family: 'Poppins';
    position: absolute;
    text-align: center;
    margin-top: calc(50vw - 80px);
    z-index: 1;
    width: calc(100vw - 84px);
    font-size: 11px;
}
.chard-holder-description span{
    font-weight: bold;
    font-size: 40px;
    line-height: 40px;
}

.add-modal{
    position: fixed;
    top: calc( 50vh - 230px);
    height: 390px;
    width: calc( 100vw - 84px);
    max-width: 420px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    background-color: white;
    border: 2px solid #FFDCDC;
    border-radius: 10px;
    box-sizing: border-box;
    padding: 27px;
}
.add-input{
    width: 100%;
}
.add-button-holder{
    text-align: center;
    width: 100%;
}
.close-button{
    position: absolute !important;
    top: 15px;
    right: 15px;
    padding: 3px !important;
    background-color: #FFAFA4 !important;
}
.clipboard{
    border: 1px solid #FFDCDC;
    border-radius: 2px;
    padding: 3px 0 3px 10px;
    font-size: 14px;
    display: grid;
    grid-template-columns: 1fr 25px;
}
.clipboard-text{
    font-family: 'Poppins';
    -webkit-user-select: all;
       -moz-user-select: all;
        -ms-user-select: all;
            user-select: all;
    overflow-x: scroll;
}
.red{
    color: #FF0000;
}
.user-modal{
    position: fixed;
    top: calc( 50vh - 190px);
    width: calc( 100vw - 50px);
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    max-width: 400px;
    background-color: white;
    border: 3px solid #FFDCDC;
    border-radius: 10px;
    box-sizing: border-box;
    padding: 60px 40px 40px 40px;
    transition: all 320ms;
}
.user-modal:focus{
    outline: none;
}
.user-input{
    width: 100%;
}
.update-button-holder{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    text-align: center;
    width: 100%;
}
.user-avatar{
    border-radius: 50%;
    height: 100px;
    width: 100px;
    position: absolute;
    top: -60px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    margin: 0;
    border: 4px solid #FFDCDC;
}
.close-button{
    position: absolute !important;
    top: 15px;
    right: 15px;
    padding: 3px !important;
    background-color: #FFAFA4 !important;
}
.user-share-profile{
    padding-left: 20px;
    text-align: center;
}
.logout-button{
    transition: all 320ms !important;
}
.logout-button:hover{
    background-color: #FF5050 !important;
    color: white !important;
}
#copy-me{
    white-space:nowrap;
}
.history-page{
    padding: 40px 40px 40px 40px;
}
.history-drink-label{
    display: grid;
    grid-template-columns: 1fr 1fr 40px;
    grid-template-rows: 25px 35px;
}
p{
    margin: 0 !important;
    font-family: Poppins;
}
.drink-place{
    color: #F68080;
}
.drink-time{
    text-align: right;
    color: grey;
    font-size: 12px;
    padding-top: 8px;
}
.drink-price{
    font-size: 24px;
    text-align: right;
}
.drink-description{
    padding-top: 10px;
    padding-bottom: 10px;
}
img{
    width: 60vw;
    background-color: #FFC9C2;
    margin: 30px calc(50% - 30vw) 0 calc(50% - 30vw);
}
.thaman-color{
    border-bottom: 2px solid #F68080;
    margin-bottom: 10px;
}
h3 span{
    color: black;
}
.history-total{
    margin-top: 25px !important;
    text-align: right;
}
.expand-icon{
    margin-top: 10px;
    color: #F68080;
}
.drink-options{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 60px;
    margin-bottom: 5px;
}
.drink-label{
    font-weight: bold;
    text-align: center;
}
.drink-label span{
    color: #8888FF;
    text-align: center;
}
.drink-date{
    text-align: center;
}
.drink-date span{
    color: #8888FF;
    font-weight: bold;
    font-size: 14px;
}
.drink-name{
    white-space: nowrap;
}
.drink-collapse{
    text-align: center;
}
.login-page{
    height: 100vh;
    width: 100vw;
    text-align: center;
    background: linear-gradient(180deg, #FC8A8A 0%, #FFD3B3 100%);
}
.login-logo{
    height: 100vw;
    max-height: 50vh;
    width: 100vw;
    max-width: 50vh;
    margin: 0 auto;
    padding-top: 20vh;
    background-image: url(/static/media/logo-shadow.0824830a.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
}
.fb-button{
    /* background-color: #4267b2; */
    background-color: inherit;
    padding: 0 25px 0 25px;
    color: #4267b2;
    border: 1.5px solid #4267b2;
    border-radius: 25px;
    font-size: 15px;
    height: 50px;
    transition: all 500ms;
}
.fb-button:hover{
    background-color: rgba(255, 255, 255, 0.2);
    padding: 0 28px 0 28px;
}
.fb-button:active{
    background-color: rgba(255, 255, 255, 0.2);
    padding: 0 28px 0 28px;
}
.bottom-nav{
    width: 100%;
    position: fixed;
    bottom: 0;
}
.center-fab{
    height: 100px;
    width: 100px;
    box-sizing: border-box;
    padding-top: 17px;
    margin-top: -35px;
    text-align: center;
    border: 5px solid #FFAFA4;
    border-radius: 50%;
    color: #FFAFA4;
    background-color: white;
    z-index: 1000;
}
.page{
    height: calc(100vh - 56px);
    box-sizing: border-box;
    width: 100vw;
    overflow-y: scroll;
    overflow-x: hidden;
    padding-bottom: 40px;
    position: relative;
}
.avatar-button{
    height: 37px;
    width: 37px;
    border-radius: 50%;
    position: absolute;
    right: -36px;
    top: 5px;
}
.loading-background{
    height: 100vh;
    width: 100vw;
    position: fixed; 
    top: 0;
    left: 0;
    background-color: rgba( 0, 0, 0, 0.5 );
}
.lds-ellipsis {
    display: inline-block;
    position: relative;
    width: 64px;
    height: 64px;
}
.loading-background{
    height: 100vh;
    width: 100vw;
    padding-top: 50vh;
    text-align: center;
}
.lds-ellipsis div {
    position: absolute;
    top: 27px;
    width: 11px;
    height: 11px;
    border-radius: 50%;
    background: #fcf;
    -webkit-animation-timing-function: cubic-bezier(0, 1, 1, 0);
            animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
.lds-ellipsis div:nth-child(1) {
    left: 6px;
    -webkit-animation: lds-ellipsis1 0.6s infinite;
            animation: lds-ellipsis1 0.6s infinite;
}
.lds-ellipsis div:nth-child(2) {
    left: 6px;
    -webkit-animation: lds-ellipsis2 0.6s infinite;
            animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(3) {
    left: 26px;
    -webkit-animation: lds-ellipsis2 0.6s infinite;
            animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(4) {
    left: 45px;
    -webkit-animation: lds-ellipsis3 0.6s infinite;
            animation: lds-ellipsis3 0.6s infinite;
}
@-webkit-keyframes lds-ellipsis1 {
    0% { -webkit-transform: scale(0); transform: scale(0); }
    100% { -webkit-transform: scale(1); transform: scale(1); }
}
@keyframes lds-ellipsis1 {
    0% { -webkit-transform: scale(0); transform: scale(0); }
    100% { -webkit-transform: scale(1); transform: scale(1); }
}
@-webkit-keyframes lds-ellipsis3 {
    0% { -webkit-transform: scale(1); transform: scale(1); }
    100% { -webkit-transform: scale(0); transform: scale(0); }
}
@keyframes lds-ellipsis3 {
    0% { -webkit-transform: scale(1); transform: scale(1); }
    100% { -webkit-transform: scale(0); transform: scale(0); }
}

@-webkit-keyframes lds-ellipsis2 {
    0% { -webkit-transform: translate(0, 0); transform: translate(0, 0); }
    100% { -webkit-transform: translate(19px, 0); transform: translate(19px, 0); }
}

@keyframes lds-ellipsis2 {
    0% { -webkit-transform: translate(0, 0); transform: translate(0, 0); }
    100% { -webkit-transform: translate(19px, 0); transform: translate(19px, 0); }
}
@-moz-document url-prefix() {
    .center-fab{
        margin-top: -5px;
    }
}
@media only screen and (max-width: 350px) {
    h2 {
        font-size: 30px !important;
    }
    h3 {
        font-size: 20px !important;
    }
    h4 {
      font-size: 20px !important;
    }
  }

